// reset scss features,, normalizing css settings for all browswer
@import 'scss-reset/_reset.scss';
// *modules: Î≥ÄÏàò, mixin Ìå®ÌÇ§ÏßÄ 

@import 'modules';
@import './layout/board';

// ! Î≠îÍ∞Ä ÏïàÎêòÎÑ§Ïö© „Ö†„Ö† üò• 
// @include font-face('NotoSansCJKkr-Regular', 'NotoSansCJKkr-Regular', 400);
// $font-stack: NotoSans, sans-serif;

::-webkit-scrollbar{
  width: 0;
  height: 0;
}
html{
  font-size: 10px; // rem Í∏∞Ï§Ä
}
// * normalize
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  // font: 100% $font-stack;

  letter-spacing: -0.5px;
  color: #2E2E2E;

  line-height: 1;
  word-break: keep-all;

  min-width: 1440px;
  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
}

// *üçöÔ∏è theme
.App{
  position: relative;
  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }    
  // * global ÏßÄÏàòÎπà
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }
  // * global Ï†ïÏàòÏßÑC 
  // ÎçîÎ≥¥Í∏∞, Ï†ÑÏ≤¥Î≥¥Í∏∞Í∞ôÏùÄ Î≤ÑÌäº
  .btn-link{
    display: inline-block;
    color: $color-2C99FF_azure;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1em;
  
    text-align: center;
  }
}


